:root{
  --background: #03045e;
}


/* ------------------------------HOME-------------------- */
.home{
  background-color:var(--background);
  min-height:88.4vh;
  color:#fff;
  text-align: center;
}
.home h1{
  font-size:2.4rem;
  margin-bottom:3rem;
}
.home p{
  font-size:1.5rem;
  font-weight: bold;
  font-style:italic;
  margin-bottom:5rem;
}
.home p span,.home p a{color:red;}
.btn{
  color:#000;
  background-color:#fff;
  padding:15px;
  text-decoration:none;
  border-radius:10px;
  font-size:1.1rem;
}
.btn:hover{
  transition:all .4s;
  background-color:#f1f1f1;
}

.banner{

  width:100vw;
  max-width:100%;
  height:88.4vh;
  max-height:100%;
  position: relative;
}
.text {
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}


/* ------------------------------DAILY-------------------- */


.btn-grad {background-image: linear-gradient(to right, #314755 0%, #26a0da  51%, #314755  100%)}
.btn-grad {
  cursor:pointer;
   margin: 10px;
   padding: 15px 45px;
   text-align: center;
   width:150px;
   font-size:1.5rem;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   box-shadow: 0 0 20px #eee;
   border-radius: 10px;
   display: block;
 }
 
#reset{
  background-image: linear-gradient(to right, #DC2424 0%, #4A569D  51%, #DC2424  100%);
  width:200px;
} 
 .reset-btn{
   display:flex;
  justify-content: center;
  align-items: center;
  }


 .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }
 

.daily {
  text-align: center;
  display:flex;
  justify-content: center;
  align-items: center;
  margin: auto 0;
  min-height: 89.2vh;
  background-color: #03045f;
}


.output{

  color:azure;
  font-size:1.5rem;
}
.daily h1{
  color:azure;
}


/* ------------------------------NAVBAR-------------------- */
nav{
  background-color: var(--background);
  color:#fff;
  display:flex;
  justify-content: space-between;
  align-items: center;
  border-bottom:1px solid rgb(235, 212, 164);
}
nav ul{
  display:flex;
  margin:0;
}
nav ul li{
  list-style: none;
  padding:20px 45px;

}
nav a{
  text-decoration:none;
  color:rgb(180, 180, 179);
}
.active{
  color:#fff;
}
.logo{
  width:100px;
  padding-left:3rem;
}
.fa-bars{
  display:none;
}
@media only screen and (min-width:760px){
  .hiddenToggle{display:none;}
  .btns-inc-dec{
    display:flex;
  }
}
  @media only screen and (max-width:760px){
  nav ul{
    flex-flow:column nowrap;
    position:fixed;
    top:100px;
    height:30vh;
    background-color: #2b353f;
    color:#000;
    width:70vw;    
    align-items: center;
    justify-content: center;
    padding:0;
    border-radius:5px;
    margin: 0 4rem;
    z-index: 1000;
  }

  li:hover{
    background-color: rgb(129, 117, 98);

  }
    .hidden{
      display:none;
    }
   
 
}



/* ----------------------------SEARCH--------------------- */
.search{
  display:flex;
  align-items: center;
  background-color:rgb(233,233,233);
  border-radius:10px;
  margin-bottom:1rem;
  padding:10px;
}
.search input{
  border:none;
  background-color: rgb(233,233,233);
  padding-left:10px;
}
.search input:focus{
  outline:none;
}



/* ----------------------------ARCHIVE--------------------- */
.archive{
  background-color:var(--background);
  height:190vh;
  padding: 1rem;
  /* max-width:960px; */
}

.note{
  white-space:pre-wrap;
  background-color: #fef68a; 
  margin-bottom:10px;
  border-radius:10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content:space-between;
}

.note-footer{
  display: flex;
  align-items:center ;
  justify-content: space-between;
}
.notes-list{
  display:grid;
  grid-gap:1rem;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  
}
.note.new{
  background-color: #67d7c9;
}
textarea{
  background-color: #67d7c9;
  border:none;
  resize:none;

}
 textarea:focus{
  outline:none;   
 }
 textarea::placeholder{
   font-size:1.1rem;
 }
.save{
  background: rgb(218, 218, 214);
  border:none;
  border-radius: 15px;
  padding:10px 10px;
  cursor:pointer;
  font-weight:bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.save:hover{
  background: #fff;
  transition:all .3s;
}

.fa-trash-alt{
  cursor:pointer;

}

.date{
  background-color:#184e77;
  border-radius: 5px;
  color:#fef68a;
  font-size:1.2rem;
  padding:15px;
}
